.LogIn {
	font: 20px/1.1 Arial, sans-serif;
	@media (max-width: 1000px) {
		font: 16px/1.1 Arial, sans-serif;
	}

	background-color: deepskyblue;
	width: 100%;
	position: relative;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	text-align: center;

	&-engee {
		position: fixed;
		top: 3vh;
		left: 2vw;
		color: aliceblue;
		font-size: 4vh;
		line-height: 4vh;
	}
	&-title {
		font-size: 3rem;
		margin-bottom: 2rem;
		color: white;
		padding: 1rem;
		@media (max-width: 500px) {
			font-size: 2rem;
		}
	}
	& .MyBtn {
		margin: 2rem 0 1rem 0;
		i {
			line-height: 2rem;
			font-size: 2.2rem;
			margin-left: 0.7rem;
		}
	}
}
